'use server';
/**
 * @fileOverview An AI agent for converting PDF files to text.
 *
 * - convertPdfToText - A function that handles the PDF to text conversion process.
 * - ConvertPdfToTextInput - The input type for the function.
 * - ConvertPdfToTextOutput - The return type for the function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const ConvertPdfToTextInputSchema = z.object({
  pdfDataUri: z
    .string()
    .describe(
      "A PDF file, as a data URI that must include a MIME type and use Base64 encoding. Expected format: 'data:application/pdf;base64,<encoded_data>'."
    ),
});
export type ConvertPdfToTextInput = z.infer<typeof ConvertPdfToTextInputSchema>;

const ConvertPdfToTextOutputSchema = z.object({
  text: z.string().describe('The extracted text from the PDF.'),
});
export type ConvertPdfToTextOutput = z.infer<typeof ConvertPdfToTextOutputSchema>;

export async function convertPdfToText(input: ConvertPdfToTextInput): Promise<ConvertPdfToTextOutput> {
  return pdfToWordFlow(input);
}

const prompt = ai.definePrompt({
  name: 'pdfToWordPrompt',
  input: {schema: ConvertPdfToTextInputSchema},
  output: {schema: ConvertPdfToTextOutputSchema},
  prompt: `You are an expert at extracting high-quality, clean text from PDF documents.
  Extract all the text content from the provided PDF file.
  Maintain the original structure, paragraphs, and line breaks as much as possible.
  Do not add any commentary or extra text that is not in the original PDF.

PDF File: {{media url=pdfDataUri}}`,
  model: 'googleai/gemini-1.5-flash-latest',
});

const pdfToWordFlow = ai.defineFlow(
  {
    name: 'pdfToWordFlow',
    inputSchema: ConvertPdfToTextInputSchema,
    outputSchema: ConvertPdfToTextOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    if (!output) {
      throw new Error('PDF to Text conversion failed: The model did not return any output.');
    }
    return output;
  }
);
