export function PdfToWordPage() {
  const [pdfFile, setPdfFile] = useState<File | null>(null);
  const [isConverting, setIsConverting] = useState(false);
  const [convertedText, setConvertedText] = useState('');
  const [isDragging, setIsDragging] = useState(false);
  const [language, setLanguage] = useState<'km' | 'en'>('en');
  const [isSupportSheetOpen, setIsSupportSheetOpen] = useState(false);
  const [isFeaturesSheetOpen, setIsFeaturesSheetOpen] = useState(false);

  const { toast } = useToast();
  const fileInputRef = useRef<HTMLInputElement | null>(null);
  const t = useMemo(() => translations[language], [language]);

  const handleFileSelect = useCallback(async (file: File | null | undefined) => {
    if (!file) return;

    if (file.type !== 'application/pdf') {
      toast({
        title: t.invalidFileType,
        description: t.selectPdfFile,
        variant: "destructive",
      });
      return;
    }

    setPdfFile(file);
    setIsConverting(true);
    setConvertedText('');

    try {
      const pdfDataUri = await blobToBase64(file);
      const result = await convertPdfToText({ pdfDataUri });

      if (result && result.text) {
        setConvertedText(result.text);
      } else {
        toast({
          title: t.conversionFailed,
          description: t.noText,
          variant: "destructive",
        });
      }
    } catch (e: any) {
      console.error(e);
      let title = t.conversionError;
      let description = e.message || "An error occurred while processing your PDF.";
      const errorMessage = (e.message || '').toLowerCase();
      if (errorMessage.includes('429') || errorMessage.includes('rate limit')) {
          title = t.rateLimitExceeded;
          description = t.rateLimitMessage;
      }
      toast({
        title: title,
        description: description,
        variant: "destructive",
      });
    } finally {
      setIsConverting(false);
    }
  }, [t, toast]);


  const handleDragEvents = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();
  };

  const handleDragEnter = (e: React.DragEvent<HTMLDivElement>) => {
    handleDragEvents(e);
    if (e.dataTransfer.items && e.dataTransfer.items.length > 0) {
      setIsDragging(true);
    }
  };

  const handleDragLeave = (e: React.DragEvent<HTMLDivElement>) => {
    handleDragEvents(e);
    setIsDragging(false);
  };
  
  const handleDrop = (e: React.DragEvent<HTMLDivElement>) => {
    handleDragEvents(e);
    setIsDragging(false);
    handleFileSelect(e.dataTransfer.files?.[0]);
  };

  const handleDownload = () => {
    if (!pdfFile || !convertedText) return;
    const blob = new Blob([convertedText], { type: 'text/plain' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `${pdfFile.name.replace('.pdf', '') || 'converted'}.txt`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const handleCopy = () => {
    if (!convertedText) return;
    navigator.clipboard.writeText(convertedText).then(() => {
        toast({ title: t.copied });
    });
  };

  const toggleLanguage = useCallback(() => {
    setLanguage(prevLang => prevLang === 'en' ? 'km' : 'en');
  }, []);

  const isReadyForContent = !isConverting && convertedText.length > 0;

  return (
    <div className="flex flex-col h-screen bg-background text-foreground">
        <header className="flex-shrink-0 flex items-center justify-between p-4 border-b">
            <h1 className="text-xl font-bold">VoiceScribe</h1>
            <div className="flex items-center gap-2">
                <Button variant="outline" size="icon" onClick={toggleLanguage}>
                    <Languages className="h-4 w-4" />
                </Button>
                <DropdownMenu>
                  <DropdownMenuTrigger asChild>
                    <Button variant="outline" size="icon">
                      <Menu className="h-4 w-4" />
                    </Button>
                  </DropdownMenuTrigger>
                  <DropdownMenuContent align="end">
                      <DropdownMenuItem onSelect={() => setIsFeaturesSheetOpen(true)}>
                        <Sparkles className="mr-2 h-4 w-4" />
                        <span>{t.features}</span>
                      </DropdownMenuItem>
                      <DropdownMenuItem onSelect={() => setIsSupportSheetOpen(true)}>
                        <Sparkles className="mr-2 h-4 w-4" />
                        <span>{t.support}</span>
                      </DropdownMenuItem>
                  </DropdownMenuContent>
                </DropdownMenu>

                <Sheet open={isFeaturesSheetOpen} onOpenChange={setIsFeaturesSheetOpen}>
                  <SheetContent side="bottom" className="h-4/5 rounded-t-lg">
                    <SheetHeader className="text-left">
                        <SheetTitle>{t.features}</SheetTitle>
                    </SheetHeader>
                  </SheetContent>
                </Sheet>

                <Sheet open={isSupportSheetOpen} onOpenChange={setIsSupportSheetOpen}>
                  <SheetContent side="bottom" className="h-4/5 rounded-t-lg">
                    <SheetHeader className="text-left">
                        <SheetTitle>{t.support}</SheetTitle>
                        <SheetDescription>
                            {t.supportDescription}
                        </SheetDescription>
                    </SheetHeader>
                    <div className="h-full w-full rounded-md border mt-4 overflow-hidden">
                      <iframe src="https://0zodesigner.github.io/donate/" className="w-full h-full" title="Donate" />
                    </div>
                  </SheetContent>
                </Sheet>
            </div>
        </header>

        <div className="flex-shrink-0 p-4 border-b">
            <div className="flex items-center gap-2 overflow-x-auto no-scrollbar pb-2">
                <Button asChild variant="outline">
                  <Link href="/">{t.voiceScribe}</Link>
                </Button>
                <Button variant="default">{t.feature2}</Button>
                <Button variant="outline">{t.feature3}</Button>
                <Button variant="outline">{t.feature4}</Button>
            </div>
        </div>

        <main 
            className="flex-grow p-4 md:p-6 grid grid-cols-1 gap-6 relative bg-muted/40 overflow-y-auto"
            onDragEnter={handleDragEnter}
            onDragOver={handleDragEvents}
            onDragLeave={handleDragLeave}
            onDrop={handleDrop}
        >
            {isConverting && (
                <div className="absolute inset-0 bg-background/80 flex flex-col items-center justify-center z-10">
                    <Loader2 className="h-12 w-12 animate-spin text-primary" />
                    <p className="text-muted-foreground mt-4 text-lg">{t.converting}</p>
                </div>
            )}

            {!pdfFile ? (
                <div 
                    className={cn(
                        "flex flex-col items-center justify-center text-center rounded-lg border-2 border-dashed border-muted-foreground/20 bg-background h-full transition-colors cursor-pointer",
                        isDragging && "border-primary bg-primary/10"
                    )}
                    onClick={() => fileInputRef.current?.click()}
                >
                    <FileUp className="w-20 h-20 text-muted-foreground/30 mb-4"/>
                    <h3 className="text-2xl font-semibold">{t.readyToConvert}</h3>
                    <p className="text-muted-foreground mt-2 mb-4">{t.dropPdf}</p>
                </div>
            ) : (
                <Card className="flex flex-col h-full shadow-sm overflow-hidden">
                    <CardContent className="p-0 flex-grow flex flex-col">
                        <Textarea
                            value={convertedText}
                            readOnly
                            placeholder={t.convertedTextPlaceholder}
                            className="h-full w-full resize-none text-base leading-relaxed p-6 border-0 focus-visible:ring-0 focus-visible:ring-offset-0 flex-grow"
                            aria-label="Converted Text"
                        />
                    </CardContent>
                </Card>
            )}
            
            <input
                type="file"
                ref={fileInputRef}
                onChange={(e) => handleFileSelect(e.target.files?.[0])}
                className="hidden"
                accept="application/pdf"
            />
        </main>

        <footer className="flex-shrink-0 flex items-center justify-center gap-2 p-4 border-t bg-background shadow-sm">
            <div className="w-full max-w-md flex gap-2 items-center">
                <Button 
                    variant="outline" 
                    size="icon" 
                    onClick={handleCopy} 
                    disabled={!isReadyForContent} 
                    className="h-14 w-14 rounded-full flex-shrink-0"
                    aria-label="Copy Text"
                >
                    <Copy className="h-6 w-6" />
                </Button>
                <div className="flex-grow">
                    <Button 
                        variant="default" 
                        size="lg" 
                        onClick={handleDownload} 
                        disabled={!isReadyForContent} 
                        className="rounded-full h-14 px-8 w-full"
                    >
                        <Download className="h-5 w-5" />
                        <span className="ml-2 sm:inline font-bold text-lg">{t.download}</span>
                    </Button>
                </div>
            </div>
        </footer>
    </div>
  );
}

    